"use client";

import React, { useState, useEffect } from "react";
import { CloudSun, Menu } from "lucide-react";
import Sidebar from "./components/Sidebar";
import CurrentWeatherCard from "./components/CurrentWeatherCard";
import HourlyForecast from "./components/HourlyForecast";
import DailyForecast from "./components/DailyForecast";
import DetailsGrid from "./components/DetailsGrid";
import { CITIES, City } from "./lib/cities";
import { FullWeatherData, generateWeatherData } from "./lib/weatherMock";
import {
  getRecentSearches, addRecentSearch,
  getFavorites, toggleFavorite,
  getLastCity, saveLastCity
} from "./lib/storage";

export default function Home() {
  // State
  const [currentCity, setCurrentCity] = useState<City>(CITIES[0]); // Default: Helsinki
  const [weather, setWeather] = useState<FullWeatherData | null>(null);
  const [favorites, setFavorites] = useState<City[]>([]);
  const [recent, setRecent] = useState<City[]>([]);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  // Initial Load
  useEffect(() => {
    // Load persisted data
    setFavorites(getFavorites());
    setRecent(getRecentSearches());

    const last = getLastCity();
    if (last) {
      setCurrentCity(last);
    }
  }, []);

  // Update Weather when City Changes
  useEffect(() => {
    // Generate deterministic mock data
    const data = generateWeatherData(currentCity);
    setWeather(data);
    saveLastCity(currentCity);
  }, [currentCity]);


  // Handlers
  const handleSelectCity = (city: City) => {
    setCurrentCity(city);
    const updatedRecent = addRecentSearch(city);
    if (updatedRecent) setRecent(updatedRecent);

    // Close mobile menu if open (implementation dependent, sidebar handles layout mostly)
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const handleToggleFavorite = () => {
    const updated = toggleFavorite(currentCity);
    setFavorites(updated);
  };

  const isCurrentFavorite = favorites.some(c => c.name === currentCity.name);

  if (!weather) return null; // Loading state (instant usually)

  return (
    <div className="min-h-screen bg-slate-50 text-slate-800 font-sans selection:bg-blue-100 selection:text-blue-900">

      <div className={`${isMobileMenuOpen ? 'fixed inset-0 z-50 bg-white overflow-y-auto' : 'hidden lg:block'}`}>
        <div className="lg:hidden p-4 flex justify-end">
          <button onClick={() => setIsMobileMenuOpen(false)} className="p-2 bg-slate-100 rounded-full"><Menu /></button>
        </div>
        <Sidebar
          favorites={favorites}
          recent={recent}
          onSelectCity={(c) => { handleSelectCity(c); setIsMobileMenuOpen(false); }}
          onRemoveFavorite={(c) => setFavorites(toggleFavorite(c))}
        />
      </div>

      {/* Mobile Header (Collapse placeholder) */}
      <div className="lg:hidden flex items-center justify-between p-4 bg-white border-b border-slate-200 sticky top-0 z-40">
        <div className="flex items-center gap-2">
          <div className="p-1.5 bg-blue-600 rounded-lg text-white">
            <CloudSun size={20} />
          </div>
          <span className="font-bold text-lg text-slate-800">Weatherly</span>
        </div>
        <button onClick={() => setIsMobileMenuOpen(true)} className="p-2 bg-white rounded-full shadow-sm border border-slate-200">
          <Menu size={20} className="text-slate-600" />
        </button>
      </div>


      {/* Main Content Area */}
      <main className="lg:ml-[320px] min-h-screen p-4 md:p-8 lg:p-12 max-w-[1600px] mx-auto">

        <div className="grid grid-cols-12 gap-8">
          {/* Left Column (Main Weather) */}
          <div className="col-span-12 xl:col-span-8 flex flex-col gap-8">
            <CurrentWeatherCard
              data={weather.current}
              isFavorite={isCurrentFavorite}
              onToggleFavorite={handleToggleFavorite}
            />
            <HourlyForecast data={weather.hourly} />
          </div>

          {/* Right Column (Details & 7-Day) */}
          <div className="col-span-12 xl:col-span-4 flex flex-col gap-8">
            <DetailsGrid data={weather.details} />
            <DailyForecast data={weather.daily} />
          </div>
        </div>
      </main>
    </div>
  );
}
