import React from "react";
import { CloudSun, Sun, Thermometer, Droplets, Wind, Star } from "lucide-react";
import { CurrentWeather } from "../lib/weatherMock";

interface Props {
    data: CurrentWeather;
    isFavorite: boolean;
    onToggleFavorite: () => void;
}

export default function CurrentWeatherCard({ data, isFavorite, onToggleFavorite }: Props) {
    const Icon = data.icon;

    return (
        <div className="col-span-12 xl:col-span-8 bg-blue-600 text-white rounded-[2rem] p-8 md:p-10 shadow-xl shadow-blue-200 relative overflow-hidden flex flex-col justify-between min-h-[400px] transition-all duration-500">
            {/* Background Decor */}
            <div className="absolute top-0 right-0 p-12 opacity-10 pointer-events-none">
                <CloudSun size={300} />
            </div>

            {/* Header */}
            <div className="relative z-10 flex flex-col md:flex-row md:items-start justify-between gap-4">
                <div className="flex items-start gap-4">
                    <div>
                        <h2 className="text-4xl md:text-6xl font-black tracking-tight mb-2 flex items-center gap-3">
                            {data.city}
                        </h2>
                        <p className="text-blue-100 text-xl font-medium">{data.date}</p>
                    </div>

                    <button
                        onClick={onToggleFavorite}
                        className="mt-2 p-2 rounded-full hover:bg-white/20 transition-colors focus:outline-none focus:ring-2 focus:ring-white/40"
                        aria-label={isFavorite ? "Remove from favorites" : "Add to favorites"}
                    >
                        <Star
                            size={32}
                            className={`transition-all ${isFavorite ? "fill-yellow-400 text-yellow-400" : "text-blue-200 hover:text-white"}`}
                        />
                    </button>
                </div>

                <div className="bg-white/20 backdrop-blur-md px-4 py-2 rounded-full text-sm font-medium border border-white/20 self-start">
                    Live Updates
                </div>
            </div>

            {/* Main Stats */}
            <div className="relative z-10 flex flex-col md:flex-row items-center md:items-end gap-6 md:gap-12 mt-8 mb-8 animate-in fade-in zoom-in duration-500">
                <div className="text-7xl md:text-9xl font-bold tracking-tighter">
                    {data.temp}°
                </div>
                <div className="flex flex-col gap-1 mb-4">
                    <div className="flex items-center gap-3 text-2xl md:text-3xl font-medium capitalize">
                        <Icon className="text-yellow-300 fill-yellow-300" size={32} />
                        {data.condition}
                    </div>
                    <div className="text-blue-100/90 text-lg">
                        {data.description}
                    </div>
                    <div className="text-blue-100 mt-1 font-medium">
                        H: {data.high}°  &bull;  L: {data.low}°
                    </div>
                </div>
            </div>

            {/* Mini Stats Footer */}
            <div className="relative z-10 grid grid-cols-3 gap-4 border-t border-white/20 pt-6">
                <div className="flex items-center gap-3">
                    <div className="p-2.5 bg-white/10 rounded-full">
                        <Thermometer size={20} className="text-blue-100" />
                    </div>
                    <div>
                        <p className="text-xs text-blue-200 uppercase font-semibold">Feels Like</p>
                        <p className="font-bold text-lg">{data.feelsLike}°</p>
                    </div>
                </div>
                <div className="flex items-center gap-3">
                    <div className="p-2.5 bg-white/10 rounded-full">
                        <Droplets size={20} className="text-blue-100" />
                    </div>
                    <div>
                        <p className="text-xs text-blue-200 uppercase font-semibold">Humidity</p>
                        <p className="font-bold text-lg">{data.humidity}%</p>
                    </div>
                </div>
                <div className="flex items-center gap-3">
                    <div className="p-2.5 bg-white/10 rounded-full">
                        <Wind size={20} className="text-blue-100" />
                    </div>
                    <div>
                        <p className="text-xs text-blue-200 uppercase font-semibold">Wind</p>
                        <p className="font-bold text-lg">{data.windSpeed} <span className="text-sm font-normal">km/h</span></p>
                    </div>
                </div>
            </div>
        </div>
    );
}
