import React from "react";
import { HourlyData } from "../lib/weatherMock";

interface Props {
    data: HourlyData[];
}

export default function HourlyForecast({ data }: Props) {
    return (
        <div className="col-span-12">
            <div className="flex items-center justify-between mb-6">
                <h3 className="text-xl font-bold text-slate-800">Hourly Forecast</h3>
                <button className="text-blue-600 text-sm font-bold hover:underline">View All</button>
            </div>

            <div className="flex overflow-x-auto pb-6 -mx-6 px-6 md:mx-0 md:px-0 gap-4 no-scrollbar">
                {data.map((hour, idx) => {
                    const Icon = hour.icon;
                    return (
                        <div
                            key={idx}
                            className={`flex-shrink-0 w-32 p-6 rounded-3xl border flex flex-col items-center gap-4 transition-all hover:-translate-y-1 hover:shadow-lg ${idx === 0
                                    ? "bg-slate-800 text-white border-slate-800 shadow-xl shadow-slate-200"
                                    : "bg-white border-slate-100 hover:border-blue-100 text-slate-600"
                                }`}
                        >
                            <span className={`text-sm font-medium ${idx === 0 ? "text-slate-300" : "text-slate-400"}`}>{hour.time}</span>
                            <Icon size={32} className={idx === 0 ? "text-white" : "text-orange-400"} />
                            <span className="text-xl font-bold">{hour.temp}°</span>
                        </div>
                    );
                })}
            </div>
        </div>
    );
}
