"use client";

import React, { useState, useEffect, useRef, useCallback } from "react";
import { Search, MapPin, X, Loader2 } from "lucide-react";
import { SearchResultItem } from "../lib/types";

interface Props {
    onSelectCity: (query: string) => void;
}

// Debounce Helper
function useDebounce<T>(value: T, delay: number): T {
    const [debouncedValue, setDebouncedValue] = useState<T>(value);
    useEffect(() => {
        const handler = setTimeout(() => setDebouncedValue(value), delay);
        return () => clearTimeout(handler);
    }, [value, delay]);
    return debouncedValue;
}

export default function SearchBox({ onSelectCity }: Props) {
    const [query, setQuery] = useState("");
    const debouncedQuery = useDebounce(query, 300);

    const [isOpen, setIsOpen] = useState(false);
    const [suggestions, setSuggestions] = useState<SearchResultItem[]>([]);
    const [isLoading, setIsLoading] = useState(false);
    const [activeIndex, setActiveIndex] = useState(-1);

    const wrapperRef = useRef<HTMLDivElement>(null);
    const abortControllerRef = useRef<AbortController | null>(null);

    // Fetch Logic
    useEffect(() => {
        if (!debouncedQuery || debouncedQuery.length < 2) {
            setSuggestions([]);
            return;
        }

        if (abortControllerRef.current) {
            abortControllerRef.current.abort();
        }
        const controller = new AbortController();
        abortControllerRef.current = controller;

        setIsLoading(true);

        fetch(`/api/search?q=${encodeURIComponent(debouncedQuery)}`, { signal: controller.signal })
            .then(res => res.json())
            .then(data => {
                if (data.results) {
                    setSuggestions(data.results);
                    setIsOpen(true);
                    setActiveIndex(-1);
                }
            })
            .catch(err => {
                if (err.name !== 'AbortError') console.error(err);
            })
            .finally(() => {
                setIsLoading(false);
            });

    }, [debouncedQuery]);

    // Click Outside
    useEffect(() => {
        function handleClickOutside(event: MouseEvent) {
            if (wrapperRef.current && !wrapperRef.current.contains(event.target as Node)) {
                setIsOpen(false);
            }
        }
        document.addEventListener("mousedown", handleClickOutside);
        return () => document.removeEventListener("mousedown", handleClickOutside);
    }, []);

    // Keyboard Nav
    const handleKeyDown = (e: React.KeyboardEvent) => {
        if (!isOpen || suggestions.length === 0) return;

        if (e.key === "ArrowDown") {
            e.preventDefault();
            setActiveIndex(prev => (prev < suggestions.length - 1 ? prev + 1 : 0));
        } else if (e.key === "ArrowUp") {
            e.preventDefault();
            setActiveIndex(prev => (prev > 0 ? prev - 1 : suggestions.length - 1));
        } else if (e.key === "Enter") {
            e.preventDefault();
            if (activeIndex >= 0) {
                selectItem(suggestions[activeIndex]);
            }
        } else if (e.key === "Escape") {
            setIsOpen(false);
        }
    };

    const selectItem = (item: SearchResultItem) => {
        // API prefers "lat,lon" for accurate forecast lookup
        const q = `${item.lat},${item.lon}`;
        onSelectCity(q);
        setQuery("");
        setIsOpen(false);
    };

    return (
        <div ref={wrapperRef} className="relative z-50 w-full group">
            <div className="absolute left-4 top-1/2 -translate-y-1/2 text-slate-400 group-focus-within:text-blue-500 transition-colors pointer-events-none">
                {isLoading ? <Loader2 size={20} className="animate-spin" /> : <Search size={20} />}
            </div>

            <input
                type="text"
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                onKeyDown={handleKeyDown}
                onFocus={() => { if (suggestions.length > 0) setIsOpen(true) }}
                placeholder="Search city (e.g., London)..."
                className="w-full bg-slate-100/80 border border-slate-200 text-slate-700 rounded-2xl py-3.5 pl-12 pr-10 focus:outline-none focus:ring-2 focus:ring-blue-100 focus:border-blue-500 transition-all font-medium placeholder:text-slate-400"
            />

            {query && (
                <button
                    onClick={() => { setQuery(""); setIsOpen(false); }}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-400 hover:text-slate-600 p-1"
                >
                    <X size={16} />
                </button>
            )}

            {/* Dropdown */}
            {isOpen && (
                <div className="absolute top-full left-0 right-0 mt-2 bg-white rounded-2xl border border-slate-100 shadow-xl max-h-[300px] overflow-y-auto no-scrollbar py-2">
                    {suggestions.length > 0 ? (
                        <ul role="listbox">
                            {suggestions.map((item, idx) => (
                                <li
                                    key={item.id}
                                    role="option"
                                    aria-selected={idx === activeIndex}
                                    className={`px-4 py-3 cursor-pointer flex items-center gap-3 transition-colors ${idx === activeIndex ? "bg-blue-50 text-blue-800" : "hover:bg-slate-50 text-slate-700"}`}
                                    onClick={() => selectItem(item)}
                                    onMouseEnter={() => setActiveIndex(idx)}
                                >
                                    <MapPin size={16} className={`shrink-0 ${idx === activeIndex ? "text-blue-500" : "text-slate-400"}`} />
                                    <div className="flex flex-col">
                                        <span className="font-medium text-sm">
                                            {item.name}
                                        </span>
                                        <span className="text-xs text-slate-400 opacity-80">{item.region ? `${item.region}, ` : ''}{item.country}</span>
                                    </div>
                                </li>
                            ))}
                        </ul>
                    ) : (
                        !isLoading && query.length >= 2 && (
                            <div className="px-4 py-6 text-center text-slate-400 text-sm">
                                No results found
                            </div>
                        )
                    )}
                </div>
            )}
        </div>
    );
}
