import React from "react";
import { Umbrella } from "lucide-react";
import { DailyData } from "../lib/weatherMock";

interface Props {
    data: DailyData[];
}

export default function DailyForecast({ data }: Props) {
    return (
        <div className="bg-white rounded-[2rem] p-8 border border-slate-100 shadow-sm h-full">
            <h3 className="text-xl font-bold text-slate-800 mb-6">7-Day Forecast</h3>
            <div className="flex flex-col gap-1">
                {data.map((day, idx) => {
                    const Icon = day.icon;
                    return (
                        <div key={idx} className="group flex items-center justify-between py-4 border-b border-slate-50 last:border-0 hover:bg-slate-50 px-2 rounded-xl transition-colors cursor-default">
                            <span className={`w-16 font-medium ${idx === 0 ? "text-blue-600 font-bold" : "text-slate-500"}`}>{day.day}</span>

                            <div className="flex items-center gap-2 flex-1 pl-4">
                                <Icon size={20} className="text-slate-400 mx-auto" strokeWidth={2} />
                                <span className="text-sm text-slate-500 font-medium hidden sm:block w-24">{day.condition}</span>
                            </div>

                            <div className="flex items-center gap-6">
                                <div className="flex items-center gap-1 w-16 text-xs font-semibold text-blue-500 bg-blue-50 px-2 py-1 rounded-full justify-center">
                                    <Umbrella size={12} />
                                    {day.rain}%
                                </div>
                                <div className="flex gap-4 w-24 justify-end text-sm">
                                    <span className="font-bold text-slate-800">{day.high}°</span>
                                    <span className="font-medium text-slate-400">{day.low}°</span>
                                </div>
                            </div>
                        </div>
                    );
                })}
            </div>
        </div>
    )
}
