import React from "react";
import { DetailData } from "../lib/weatherMock";

interface Props {
    data: DetailData[];
}

export default function DetailsGrid({ data }: Props) {
    return (
        <div className="grid grid-cols-2 gap-4">
            {data.map((detail, idx) => {
                const Icon = detail.icon;
                return (
                    <div key={idx} className="bg-white p-6 rounded-3xl border border-slate-100 shadow-sm flex flex-col gap-4 hover:shadow-md transition-shadow">
                        <div className="flex items-center justify-between text-slate-400">
                            <span className="text-xs font-bold uppercase tracking-wider">{detail.label}</span>
                            <Icon size={18} />
                        </div>
                        <div className="text-xl font-bold text-slate-800">{detail.value}</div>
                    </div>
                );
            })}
        </div>
    )
}
