import React, { useState } from "react";
import { CloudSun, History, MapPin, X } from "lucide-react";
import { City } from "../lib/cities";
import SearchBox from "./SearchBox";
import { Sun } from "lucide-react"; // Fallback icon for list

interface Props {
    favorites: City[];
    recent: City[];
    onSelectCity: (city: City) => void;
    onRemoveFavorite: (city: City) => void;
}

export default function Sidebar({ favorites, recent, onSelectCity, onRemoveFavorite }: Props) {
    const [showAllFavorites, setShowAllFavorites] = useState(false);

    return (
        <>
            <aside className="w-full lg:w-[320px] lg:h-screen lg:fixed lg:left-0 lg:top-0 bg-white border-b lg:border-b-0 lg:border-r border-slate-200 p-6 flex flex-col gap-8 z-20 overflow-visible lg:overflow-y-auto no-scrollbar">
                {/* Logo */}
                <div className="flex items-center gap-3 px-2">
                    <div className="p-2 bg-blue-600 rounded-xl text-white">
                        <CloudSun size={24} strokeWidth={2.5} />
                    </div>
                    <h1 className="text-2xl font-bold text-slate-800 tracking-tight">Weatherly</h1>
                </div>

                {/* Search */}
                <SearchBox onSelectCity={onSelectCity} />

                {/* Favorites */}
                <div className="space-y-4">
                    <div className="flex items-center justify-between px-2 text-sm font-semibold text-slate-400 uppercase tracking-wider">
                        <span>Favorites</span>
                        {favorites.length > 3 && (
                            <button onClick={() => setShowAllFavorites(true)} className="text-blue-500 hover:text-blue-600 text-xs font-bold">
                                View All ({favorites.length})
                            </button>
                        )}
                    </div>

                    {favorites.length === 0 && (
                        <div className="text-xs text-slate-400 px-2 italic">Search and click star to add favorites</div>
                    )}

                    <div className="flex flex-col gap-3">
                        {favorites.slice(0, 3).map((fav) => (
                            <button
                                key={fav.name}
                                onClick={() => onSelectCity(fav)}
                                className="flex items-center justify-between bg-slate-50 hover:bg-blue-50/50 p-4 rounded-2xl border border-transparent hover:border-blue-100 transition-all group text-left"
                            >
                                <div className="flex items-center gap-3">
                                    <div className="bg-white p-2 rounded-full shadow-sm text-yellow-500">
                                        <Sun size={16} fill="currentColor" className="text-yellow-400" />
                                    </div>
                                    <div>
                                        <div className="font-bold text-slate-700">{fav.name}</div>
                                        <div className="text-xs text-slate-400 font-medium">{fav.country}</div>
                                    </div>
                                </div>
                            </button>
                        ))}
                    </div>
                </div>

                {/* Recent Searches */}
                <div className="flex-1 space-y-4">
                    <div className="flex items-center justify-between px-2 text-sm font-semibold text-slate-400 uppercase tracking-wider">
                        <span>Recent</span>
                        <History size={14} />
                    </div>

                    {recent.length === 0 && (
                        <div className="text-xs text-slate-400 px-2 italic">No recent searches</div>
                    )}

                    <ul className="space-y-1">
                        {recent.map((item) => (
                            <li key={item.name}>
                                <button
                                    onClick={() => onSelectCity(item)}
                                    className="w-full flex items-center gap-3 px-4 py-3 text-slate-600 hover:bg-slate-50 hover:text-slate-900 rounded-xl transition-colors text-sm font-medium"
                                >
                                    <MapPin size={16} className="text-slate-400" />
                                    <span className="truncate">{item.name}, {item.country}</span>
                                </button>
                            </li>
                        ))}
                    </ul>
                </div>

                {/* Footer / Tip */}
                <div className="mt-auto pt-6 border-t border-slate-100 px-2 hidden lg:block">
                    <p className="text-xs text-slate-400 leading-relaxed">
                        <span className="font-semibold text-slate-500">Pro Tip:</span> Press <kbd className="font-sans bg-slate-100 px-1 rounded border border-slate-200">K</kbd> to search anytime.
                    </p>
                </div>
            </aside>

            {/* View All Favorites Modal */}
            {showAllFavorites && (
                <div className="fixed inset-0 z-[100] flex items-center justify-center bg-black/20 backdrop-blur-sm p-4">
                    <div className="bg-white rounded-3xl shadow-2xl w-full max-w-md overflow-hidden animate-in fade-in zoom-in-95 duration-200">
                        <div className="flex items-center justify-between p-6 border-b border-slate-100">
                            <h2 className="text-lg font-bold text-slate-800">All Favorites</h2>
                            <button onClick={() => setShowAllFavorites(false)} className="p-2 hover:bg-slate-100 rounded-full text-slate-500">
                                <X size={20} />
                            </button>
                        </div>
                        <div className="max-h-[60vh] overflow-y-auto p-4 space-y-2">
                            {favorites.map(fav => (
                                <div key={fav.name} className="flex items-center justify-between p-3 rounded-2xl bg-slate-50 hover:bg-blue-50 transition-colors group">
                                    <button onClick={() => { onSelectCity(fav); setShowAllFavorites(false); }} className="flex-1 text-left font-bold text-slate-700">
                                        {fav.name}, <span className="text-slate-400 font-normal">{fav.country}</span>
                                    </button>
                                    <button onClick={() => onRemoveFavorite(fav)} className="p-2 text-slate-300 hover:text-red-500 hover:bg-red-50 rounded-full transition-colors">
                                        <X size={16} />
                                    </button>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            )}
        </>
    );
}
